<template>
	<v-container grid-list-lg>
		<v-layout row wrap>
			<v-flex xs12 md6 offset-md3>
				<v-card v-if="success" class="elevation-3">
					<v-alert color="success" icon="check" value="true">Mensagem enviada! Entraremos em contato em breve.</v-alert>
					<v-card-actions>
						<v-btn color="primary" @click="novaMensagem">Enviar outra</v-btn>
					</v-card-actions>
				</v-card>
				<v-card v-else class="elevation-3">
					<v-card-title>
						<div class="headline">Mensagem</div>
					</v-card-title>
					<v-card-text>
						<v-form ref="form" @submit="enviar">
							<v-text-field prepend-icon="mdi-account" required v-model="nome" :rules="[rules.required]" label="Nome"></v-text-field>
							<v-text-field prepend-icon="mdi-email" required v-model="email" :rules="[rules.required, rules.email]" label="E-mail"></v-text-field>
							<v-text-field prepend-icon="mdi-phone" v-model="telefone" mask="###########" :rules="[rules.telefone]" label="Telefone"></v-text-field>
							<v-text-field prepend-icon="mdi-message-text" required v-model="mensagem" :rules="[rules.required]" label="Mensagem" multi-line></v-text-field>
						</v-form>
					</v-card-text>
					<v-card-actions>
						<v-btn color="primary" @click="enviar">Enviar</v-btn>
					</v-card-actions>
				</v-card>
			</v-flex>
		</v-layout>
		<v-dialog v-model="loading" persistent max-width="300">
			<loading></loading>
		</v-dialog>
		<v-snackbar color="error" v-model="error">{{errorText}}</v-snackbar>
	</v-container>
</template>

<script>
	import regex from '../regex.js'
	export default {
		data: () => ({
			error: false,
			success: false,
			errorText: '',
			loading: false,
			nome: '',
			email: '',
			telefone: '',
			mensagem: '',
			rules: {
				required: v => !!v || 'Campo obrigatrio!',
				email: v => regex.email.test(v) || 'E-mail invlido!',
				telefone: v => regex.telefone.test(v) || 'Telefone invlido!'
			}
		}),
		methods: {
			enviar(){
				if(this.$refs.form.validate()){
					this.loading = true
					this.$axios.post(`${this.$server}/mensagem`, {
						nome: this.nome,
						email: this.email,
						telefone: this.telefone,
						mensagem: this.mensagem
					})
					.then(() => {
						this.success = true
						this.loading = false
					})
					.catch(err => {
						this.errorText = err.response.data
						this.error = true
						this.loading = false
					})
				}
			},
			novaMensagem(){
				this.mensagem = ''
				this.success = false
			}
		}
	}
</script>



